import React, {useEffect, useState} from 'react'
import {API_KEY, imageUrl} from '../../Constants/constants'
import './RowPost.css'
import axios from '../../axios'
import YouTube from 'react-youtube'

function RowPost(props) {
    const [movies, setMovies] = useState([])
    const [urlId, setUrlId]= useState('')
    useEffect(() => {
        axios.get(props.url).then(response=>{
            //console.log(response.data.results[0])
            setMovies(response.data.results)
        })
        // }).catch(err=>{
        //     alert('Authorization error')
        // })
    }, [])
    const opts = {
        height: '390',
        width: '100%',
        playerVars: {
          // https://developers.google.com/youtube/player_parameters
          autoplay: 1,
        },
      };

      const movieClick=(id)=>{
        console.log(id)
        axios.get(`movie/${id}/videos?language=en-US&api_key=${API_KEY}`).then(response=>{
            console.log(response.data)
            if(response.data.results)
                setUrlId(response.data.results[0])
            else
                console.log('Array empty', response.data.results.length)
        })
      }

    return (
        <div className='row'>
            <h2>{props.title}</h2>
            <div className="posters">
                {movies.map((obj)=>
                    <img onClick={()=>movieClick(obj.id)} className={props.isSmall? 'small_poster': 'poster'} src={`${imageUrl+obj.backdrop_path}`} alt="" />
                )}
                
            </div>
            {urlId && <YouTube opts={opts} videoId={urlId.key}/> }
        </div>
    )
}

export default RowPost
